const BoxList = (props) => {
  let showData = props.showdata
  let toggledisplay = true
  return (
    <ul>
      {props.tasks.map((task, index) => {
        if (index >= showData)
          toggledisplay = false
        return (
          <BoxListUnit
            key={task.id}
            title={task.title}
            contents={task.contents}
            email={task.email}
            date={task.date}
            res={task.res}
            comments={task.comments}
            status={task.status}
            priority={task.priority}
            staff={task.staff}
            id={task.id}
            display={toggledisplay ? 'block' : 'none'}
          />
        )
      }
      )}
    </ul>
  )
}

class Box extends Component {
  state = {
    showingData: 5
  }

  showMoreData = () => {
    this.setState(state => ({
      showingData: state.showingData + 5
    }))
    console.log('showingData: ' + this.state.showingData)
  }

  render() {
    let showMoreBtn = true
    if (this.state.showingData > this.props.tasks.length) {
      showMoreBtn = false
    }
    return (
      <section>
        <Header />
        <PageTitle txt='' />
        <FormSelector forms={this.props.tasks} />
        <SearchBox />
        <ToolBar margin>
          <a href="/filter">
            <Button txt='' />
          </a>
        </ToolBar>
        <BoxList tasks={this.props.tasks} showdata={this.state.showingData} />
        { showMoreBtn && (
          <ToolBar margin>
            <Button txt='' btnwidth='100%' func={this.showMoreData} />
          </ToolBar>
        )}
        <BacktoTop />
        <JumptoPC />
      </section>
    )
  }
}

