const StyledTaskHead = styled.div`
  border-bottom: 1px dashed #000;
  margin-bottom: 10px;
`
const StyledTaskIdDate = styled.p`
  font-size: 12px;
`
const StyledSender = styled.div`
  display: flex;
`
const StyledMsgItems = styled.dl`
  margin: 4px 0;
  dt {
    font-size: 12px;
    color: #666;
    font-weight: bold;
  }
`
const StyledTaskDetail = styled.div`
  padding: 0 5px;
  border-bottom: 1px solid #000;
`
const StyledReplyArea = styled.ul`
  margin: 5px;
`

const ReplyArea = (props) => {
  return (
    props.tasks.map((task, index) => {
      if ((task.id == props.currentid) && task.replylist)
        return (
          task.replylist.map((reply, index) => (
            <StyledReplyArea>
              <li>
                <StyledSender>
                  <Avatar size='20px' />
                  <span>{reply.sender}</span>
                </StyledSender>
              </li>
              <li>
                <StyledTaskIdDate>{reply.date}</StyledTaskIdDate>
              </li>
              <li>{reply.comment}</li>
            </StyledReplyArea>
          ))
        )
    })
  )
}

const TaskDetail = (props) => {
  return (
    props.tasks.map((task, index) => {
      if (task.id == props.currentid)
        return (
          <StyledTaskDetail key={index}>
            <StyledTaskHead>
              <p>{task.title}</p>
              <StyledTaskIdDate>No.{task.id} {task.date}</StyledTaskIdDate>
            </StyledTaskHead>
            <StyledSender>
              <Avatar size='20px' />
              <span></span>
            </StyledSender>
            <StyledMsgItems>
              <dt></dt>
              <dd>{task.email}</dd>
            </StyledMsgItems>
            <StyledMsgItems>
              <dt></dt>
              <dd>{task.contents}</dd>
            </StyledMsgItems>
          </StyledTaskDetail>
        )
    })
  )
}

const Task = (props) => {
  return (
    <section>
      <Header />
      <PageTitle txt='' link='/' />
      <TaskDetail tasks={props.tasks} currentid={props.match.params.id} />
      {/*
      <ToolBar border margin>
        <Button txt='' />
      </ToolBar>
      */}
      <ReplyArea tasks={props.tasks} currentid={props.match.params.id} />
      <ToolBar>
      <Link to={`${props.match.url}/taskreply`}>
        <Button txt='' />
      </Link>
      </ToolBar>
      <ToolBar>
        <Link to={`${props.match.url}/arrangetask`}>
          <Button txt='' />
        </Link>
      </ToolBar>
      <BacktoTop />
      <JumptoPC />
    </section>
  )
}

