const StyledSectionTitle = styled.h3`
  margin: 0;
  padding: 5px;
  background-color: #333;
  color: #fff;
  border: 1px solid #ccc;
  border-left-width: 0;
  border-right-width: 0;
`
const StyledFieldSet = styled.fieldset`
  border: 0;
  margin: 0;
  padding: 0;
  label {
    display: inline-block;
    padding: 10px;
    width: 100%;
    border-bottom: 1px solid #ccc;
    input {
      margin-right: 4px;
    }
    &:last-child {
      border-bottom: 0;
    }
  }
`
const FilterForms = (props) => {
  return (
    <section>
      <Header />
      <PageTitle txt='' link='/' />

      <StyledSectionTitle></StyledSectionTitle>
      <StyledFieldSet action="">
        {props.filter_items.map((item, index) => {
          return (
            <label htmlFor={item.id} key={index}>
              <input type="radio" id={item.id} name="filter" />
              {item.label}
            </label>
          )
        })}
      </StyledFieldSet>
      <StyledSectionTitle></StyledSectionTitle>
      <StyledFieldSet action="">
        {props.sort_methods.map((method, index) => {
          return (
            <label htmlFor={method.id} key={index}>
              <input type="radio" id={method.id} name="sort" />
              {method.label}
            </label>
          )
        })}
      </StyledFieldSet>

      <ToolBar>
        <a href="/">
          <Button txt='' />
        </a>
        <Button txt='' />
      </ToolBar>
    </section>
  )
}

